body {
  margin: 0;
  padding: 0;
}

.brikwars-app {
  width: 3200px;
  height: 3200px;
  box-sizing: border-box;
  padding: 100px;
  background-color: #000;

  * {
    box-sizing: border-box;
  }
}

.brikwars-app__map {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  width: 3000px;
  height: 3000px;
}

.brikwars-app__module {
  --path-color: #eee;
  --background-color: #a0bcac;
  position: relative;

  display: grid;
  grid-template-areas: ". up ." "left players right" ". down .";
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 2fr 1fr;
  font-size: 1em;
  background-color: var(--background-color);
  background-image: linear-gradient(0deg, var(--path-color), var(--path-color)),
    linear-gradient(0deg, var(--path-color), var(--path-color));
  background-repeat: no-repeat;
  background-size: 0 0;
  box-shadow: 0 0 0 2px purple;
}

.brikwars-app__module--lab {
}

.brikwars-app__module--mine {
  --path-color: #e4cd93; // tan
  --background-color: #d09168; // nougat
}

.brikwars-app__module--secure {
  --path-color: #444;
  --background-color: #aaa;
  color: #fff;
  // nougat
}

.brikwars-app__module--selected {
  z-index: 10;
}

.brikwars-app__module--room {
  background-size: 100% 100%;
}

.brikwars-app__module--corridorT {
  background-size: 100% 50%, 50% 25%;
  background-position: 0 50%, 50% 100%;

  .brikwars-app__move-button--up {
    display: none;
  }
}

.brikwars-app__module--corridorT-left {
  background-size: 50% 100%, 25% 50%;
  background-position: 50% 0, 0 50%;

  .brikwars-app__move-button--right {
    display: none;
  }
}
.brikwars-app__module--corridorT-right {
  background-size: 50% 100%, 25% 50%;
  background-position: 50% 0, 100% 50%;

  .brikwars-app__move-button--left {
    display: none;
  }
}

.brikwars-app__module--corridorT-up {
  background-size: 100% 50%, 50% 25%;
  background-position: 0 50%, 50% 0%;

  .brikwars-app__move-button--down {
    display: none;
  }
}

.brikwars-app__module--straight {
  background-size: 100% 50%, 10% 10%;
  background-position: 0 50%, 50% 8%;

  .brikwars-app__move-button--up,
  .brikwars-app__move-button--down {
    display: none;
  }
}

.brikwars-app__module--straight-90 {
  background-size: 50% 100%, 10% 10%;
  background-position: 50% 0, 8% 50%;

  .brikwars-app__move-button--right,
  .brikwars-app__move-button--left {
    display: none;
  }
}

.brikwars-app__mod-label {
  position: absolute;
  top: 1rem;
  left: 1rem;
}

.brikwars-app__players {
  grid-area: players;
}

.brikwars-app__player {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  color: var(--text-color, #000);
  border: 1px solid currentColor;
  border-radius: 100%;
  background-color: var(--background-color);
}

.brikwars-app__move-button {
  border: 0;
  background: rgba(255, 255, 255, 0.5);

  &[disabled] {
    background: transparent;
  }
}

.brikwars-app__move-button--up {
  grid-area: up;
}

.brikwars-app__move-button--right {
  grid-area: right;
}

.brikwars-app__move-button--down {
  grid-area: down;
}

.brikwars-app__move-button--left {
  grid-area: left;
}
